code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hashtag {
  border: none;
  width: 95%;
  height: 30px;
  border-radius: 5px;
  box-shadow: 3px 3px 8px 0px #c1c1c1;
}

.hashtag:focus {
  border: none;
  box-shadow: none;
  box-shadow: 1px 1px 3px 0px #c1c1c1;
}

.hashtag-icon {
  border: none;
  border-radius: 0px;
  font-size: 16px;
  font-family: "Montserrat";
  box-shadow: 3px 3px 8px 0px #c1c1c1;
}

.submit {
  background-color: #00aced;
  border: none;
  border-radius: 0px;
  color: white;
  font-size: 16px;
  padding: 8px 20px;
  font-family: "Montserrat";
}

.submit:hover {
  background-color: #0084b4;
}

.submit:active {
  border: none;
  box-shadow: none;
  background-color: #0084b4;
}

.tweets {
  background-color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 8px 0px #ededed;
  padding: 5px 15px;
  margin-bottom: 20px;
}

.tweets h2 {
  color: #0084b4;
  font-size: 16px;
}

.tweets h3 {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.footer {
  background-color: #f9f9f9;
  height: 40px;
  width: inherit;
  padding: 0;
  position: fixed;
  bottom: 0px;
  text-align: center;
}

.progressheader {
  margin-top: 10px;
  font-size: 14px;
  color: #00aced;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.heading_desc {
  font-family: "Montserrat";
  font-weight: 300;
  opacity: 0.6;
}
